<template>
  <div class="register">
    <div class="back" @click="back">
      <van-icon name="arrow-left" />
    </div>
    <div class="title">注册</div>
    <!-- 注册 -->
    <van-form>
      <van-field
        v-model="account"
        name="手机号"
        label="手机号"
        placeholder="请输入手机号"
        autocomplete="off"
        show-error= false,
        :rules="[{ required: true, message: '请填写手机号' }]"
      />
      <van-field
        v-model="username"
        type="text"
        name="昵称"
        label="昵称"
        placeholder="请输入昵称"
        autocomplete="off"
        :rules="[{ required: true, message: '请输入昵称' }]"
      />
      <van-field
        v-model="password"
        :type="!isOpenRegister ? 'password' : 'text'"
        name="密码"
        label="密码"
        placeholder="请输入密码密码"
        :right-icon="!isOpenRegister ? 'closed-eye' : 'eye-o'"
        @click-right-icon="toggleRegisterPasssword"
        autocomplete="off"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      
      <van-field
        v-model="repassword"
        :type="!isOpenReRegister ? 'password' : 'text'"
        name="确认密码"
        label="确认密码"
        placeholder="请再次输入密码"
        :right-icon="!isOpenReRegister ? 'closed-eye' : 'eye-o'"
        @click-right-icon="toggleRegisterPasssword2"
        autocomplete="off"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" class="btn" native-type="submit" @click="setstorager">注册</van-button>
      </div>

    </van-form>
  </div>  
</template>

<script>
import { Toast } from 'vant'
  export default {
    name: 'Register',
    data() {
      return {
        isOpenReRegister: false,
        isOpenRegister: false,
        account: '',
        password: '',
        repassword: '',
        username: '',
        flag: true
      }
    },
    methods: {
      back() {
        this.$router.go(-1)
      },
      //注册
      setstorager () {
        
        if(this.account && this.password && this.repassword === '')  {
          Toast('请输入账号和密码');
          return;
        }

        if (this.password != this.repassword) {
          Toast('密码和确认密码不相同');
          return;
        }

        let data = {
          account: this.account,
          password: this.password,
          repassword: this.repassword,
          username: this.username
        }
        let dataJson = JSON.stringify(data);
        localStorage.setItem('data', dataJson);
        this.$router.push({name: 'Login'})
      },

      toggleRegisterPasssword () {
        this.isOpenRegister = !this.isOpenRegister;
      },

      toggleRegisterPasssword2 () {
        this.isOpenReRegister = !this.isOpenReRegister;
      }
    }
  }
</script>

<style lang="less" scoped>
.btn{
  background-color: #facc30;
}
/deep/.van-button--info{
  border: #facc30 ;
}
  .register {
    .back {
      padding: 10px;
      width: 20px;
      height: 20px;
    }
    .title {
      padding: 20px;
      font-size: 30px;
      font-weight: bold;
      margin-top: 150px;
    }
    .register {
      display: block;
      border: none;
    }
  }
</style>
